function App() {
    return (
        <div className="bg-purple-600 p-8 rounded-lg shadow-lg w-80">
            <div className="text-center mb-6">
                <i className="fas fa-store text-4xl text-white"></i>
                <h1 className="text-2xl font-bold text-white mt-2">EASY SHOP</h1>
            </div>
            <form>
                <div className="mb-4">
                    <label className="block text-white mb-2" htmlFor="email">Email</label>
                    <input className="w-full p-2 rounded border border-gray-300" type="email" id="email" placeholder="Email" />
                </div>
                <div className="mb-6">
                    <label className="block text-white mb-2" htmlFor="password">Password</label>
                    <input className="w-full p-2 rounded border border-gray-300" type="password" id="password" placeholder="Password" />
                </div>
                <button className="w-full bg-black text-white p-2 rounded" type="submit">Login</button>
            </form>
        </div>
    );
}